@import url("https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap&family=Barlow:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Raleway:ital,wght@0,100..900;1,100..900&family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap");
.App {
  text-align: center;
  /* background: #0c0d13; */
  /* background: #1F2232; */
  background: #181a26;
  overflow-x: hidden;
}

.Navbar-wrapper {
   background: #08090d;
    z-index: 1000;
    font-weight: 300;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    position: fixed;
    top: 0;
}

.App-header {
  font-family: "Major Mono Display", monospace;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 36px;
  margin: 0 5rem;
}

.App-byline {
  font-family: "Roboto Mono", monospace;
  font-weight: 300;
}

.App-footer {
  font-style: italic;
  font-size: 24px;
  font-weight: 900;
}

a {
  font-family: "Roboto Mono", monospace;
  font-weight: 300;
  color: #6b7fd7;
}



@media screen and (max-width: 476px) {
  .App-header {
    font-size: 14px;
  }
  .App-footer {
    font-size: 10px;
  }
}

.App-link {
  color: #61dafb;
}





.About-container {
  flex-direction: row;
}

.About-container, .Bio-container {
  margin: 0 5rem;
  display: flex;
  color: white;
  /* flex-direction: row; */
  align-items: center;
  margin-top: 3rem;

  background: rgb(24, 26, 38);
  @media screen and (max-width: 1000px) {
    flex-direction: column;
    margin: 0 .5rem;
    align-items: center;
  }
}
.About-content {
  text-align: left;
  flex-basis: 60%;
  font-family: "Roboto Mono", monospace;
  margin: 0;
}

.Skills-content {
  display: flex;
}

.Experience-container {
  margin: 0 10rem;
  display: flex;
  color: white;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}

.Experience-content-heading {
  display: flex;
  flex-direction: column;
  & > strong {
    font-size: 18px;
  }
}

.box-wrapper-left-bio {
  -webkit-transition-duration: 300ms;
  transition-duration: 300ms;
}
.box-wrapper-left-text {
  -webkit-transition-duration: 600ms;
  transition-duration: 600ms;
}
.box-wrapper-right {
  -webkit-transition-duration: 900ms;
  transition-duration: 900ms;
}

.logo-trans-wrapper-1 {
  -webkit-transition-duration: 1200ms;
  transition-duration: 1200ms;
  &.loading {
    transform: translateY(-1000%);
    -webkit-transform: translateY(-400%); 
  }

}
.logo-trans-wrapper-2 {
  -webkit-transition-duration: 1500ms;
  transition-duration: 1500ms;
  &.loading {
    transform: translateY(-1000%);
    -webkit-transform: translateY(400%); 
  }
}
.logo-trans-wrapper-3 {
  -webkit-transition-duration: 1800ms;
  transition-duration: 1800ms;
  &.loading {
    transform: translateY(-1000%);
    -webkit-transform: translateY(-400%); 
  }
}





.box-wrapper.loading.box-wrapper-left-bio,  .box-wrapper.loading.box-wrapper-left-text{
  transform: translate(-1000%);
  -webkit-transform: translate(-400%);    
}

.box-wrapper.loading.box-wrapper-right {
  transform: translate(1000%);        
  -webkit-transform: translate(400%);
}

.Bio-container {
  overflow: hidden;
  flex-direction: row;
  margin: 0 5rem;
  /* left: 0; */
  width: auto;
}

.Bio-left {
  flex-basis: 40%;
  /* opacity: 0; */
  /* transition: all 1s; */
}

.Bio-right {
  /* opacity: 0; */
  /* transition: all 5.5s; */
  flex-basis: 60%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 5rem;
  /* flex-direction: column; */
  /* & > div {
    flex-basis: 33%
  } */
}

.Bio-logos__first-col {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  align-items: center;
  justify-content: center;
  & > div {
    height: 140px;
    width: 160px;
    background: radial-gradient(circle at 100% 100%, #181a26 0, #181a26 3px, transparent 3px) 0% 0%/8px 8px no-repeat,
            radial-gradient(circle at 0 100%, #181a26 0, #181a26 3px, transparent 3px) 100% 0%/8px 8px no-repeat,
            radial-gradient(circle at 100% 0, #181a26 0, #181a26 3px, transparent 3px) 0% 100%/8px 8px no-repeat,
            radial-gradient(circle at 0 0, #181a26 0, #181a26 3px, transparent 3px) 100% 100%/8px 8px no-repeat,
            linear-gradient(#181a26, #181a26) 50% 50%/calc(100% - 10px) calc(100% - 16px) no-repeat,
            linear-gradient(#181a26, #181a26) 50% 50%/calc(100% - 16px) calc(100% - 10px) no-repeat,
            linear-gradient(116deg, #fa9f42 0%, #0b6e4f 83%, #E0E0E2 100%);
    border-radius: 8px;
    padding: 9px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: .325rem;
    padding: .325rem 0;
    text-transform: uppercase;
  }
}

.Bio-logos__logo-container {
  display: flex !important;
}
.Bio-left > .About-content {
  padding-left: 7rem;
}

.Logo-image {
  height: auto;
  width: 4rem;
}

.About-byline {
  flex-basis: 40%;
  font-size: 48px;
  font-family: "Major Mono Display", monospace;
  
  font-weight: 300;
  text-align: left;
  padding-left: 7rem;
  @media screen and (max-width: 1000px) {
    padding-left: unset;
    text-align: center;
  }
  & > strong {
    font-weight: 900
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.divider {
  margin: 5rem 10rem;
}

.Contact-footer {
  justify-content: center;
  display: flex;
  flex-direction: row;
  padding-bottom: 5rem;
  & > strong {
    font-size: 24px;
    font-family: "Roboto Mono", monospace;
    background: none;
    border: none;
    padding: 0px 36px;
    margin: 12px 0;
    color: white;
  }
}

.arrows {
  width: 60px;
  height: 72px;
  position: absolute;
  left: 50%;
  margin-left: -30px;
  bottom: 20px;
}

.arrows path {
  stroke: #2994d1;
  fill: transparent;
  stroke-width: 1px;
  animation: arrow 2s infinite;
  -webkit-animation: arrow 2s infinite;
}

@keyframes arrow {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes arrow /*Safari and Chrome*/ {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.arrows path.a1 {
  animation-delay: -1s;
  -webkit-animation-delay: -1s; /* Safari 和 Chrome */
}

.arrows path.a2 {
  animation-delay: -0.5s;
  -webkit-animation-delay: -0.5s; /* Safari 和 Chrome */
}

.arrows path.a3 {
  animation-delay: 0s;
  -webkit-animation-delay: 0s; /* Safari 和 Chrome */
}
